<template>
    <div v-if="redObj" class="redTop_top">
      <div class="redTop_img_box">
        <img class="redTop_mask" src="../assets/img/receive_mask.png" alt="">
        <div class="redTop_node">{{$t('ENU Red Envelope')}}</div>
        <img class="redTop_logo" src="../assets/img/logo.png" alt="">
      </div>
      <div class="from_box">{{$t('From')}} {{redObj.creator}}</div>
      <div class="total_box">
        <span>{{$t('Total')}}</span>
        <span class="amount">{{redObj.total_quantity}}</span>
        <span class="type">
          <template v-if="redObj.type == 1">{{$t('Normal')}}</template>
          <template v-else>{{$t('Random')}}</template>
        </span>
      </div>
      <div class="message_box">{{redObj.words}}</div>
      <div class="time_box">
        <div>{{$t('Create')}}：{{redObj.create_time | time(0)}}</div>
        <div class="time_num">
          <i class="el-icon-time"></i>
          <span>{{ timer }}</span>
        </div>
      </div>
    </div>

</template>

<script>
import Enu from 'enujs';
export default {
    props: {
      redObj: Object
    },
    data () {
      return {
        timer: ""
			}
    },
    created(){
      let _this = this;
      setInterval(function(){
        if(_this.redObj){
          let timeStamp = new Date().getTime();
          _this.timer = _this.setTime(_this.redObj.expire_time*1000 - timeStamp);
        }
      },1000)

    },

    methods:{
      setTime(timeStamp){
        if(timeStamp < 0){
          return "00:00";
        }else{
          let hour  = Math.floor(timeStamp/1000/60/60);
          let mintu  = Math.floor(timeStamp/1000/60%60);
          return (hour < 10 ? '0'+hour : hour) + ":" + (mintu < 10 ? "0"+ mintu : mintu);
        }
      }
    },
    computed: {

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @rem: round(1/75*2,6)*1rem;
      .redTop_top{
        background: #f8f8f8;
        .redTop_img_box{
          width: 100%;
          position: relative;
          .redTop_mask{
            width: 100%;
          }
          .redTop_node{
            position: absolute;
            top: 19%;
            left: 0;
            width: 100%;
            text-align: center;
            font-size: 22*@rem;
            color: #fff3d3;
          }
          .redTop_logo{
            height: 34*@rem;
            position: absolute;
            bottom: 14*@rem;;
            left: 50%;
            transform: translate(-50%,0);
          }

        }

        .from_box{
          font-size: 12*@rem;
          color: #5d4220;
          text-align: center;
          margin-top: 8*@rem;
        }
        .total_box{
          font-size: 12*@rem;
          color: #5d4220;
          text-align: center;
          margin-top: 8*@rem;
          .amount{
            font-size: 16*@rem;
             margin: 0 6*@rem;
          }
          .type{
            background-color: #e4f1fe;
            font-size: 12*@rem;
            color: #288efb;
            padding: 0 2*@rem;
          }
        }
        .message_box{
          font-size: 12*@rem;
          color: #5d4220;
          line-height: 15*@rem;
          text-align: center;
          padding: 8*@rem 30*@rem;
        }
        .time_box{
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 16*@rem;
          font-size: 12*@rem;
          color: #c9c2b7;
          .time_num{
            color: #ff9200;
            background-color: #fff5e8;
            padding: 2*@rem 3*@rem;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
      }
</style>



